class NavbarController {
  constructor($scope, $rootScope, $translate, $location) {
  	"ngInject";

    this.name = 'navbar';
    this.$rootScope = $rootScope;
    this.username = "";

    //var cookie = $cookieStore.get('globals');
    this.user = this.$rootScope.globals.currentUser;
    if (this.$rootScope.globals.currentUser) {
      console.log("asdasdsa");
      this.username = this.$rootScope.globals.currentUser.username;
    } else {
      console.log("2131112");
    }
    // if (this.$rootScope.globals) {
    //   this.username = this.$rootScope.globals.currentUser.username;
    // }

    

	  $scope.isActive = function (viewLocation) {
	    var active = (viewLocation === $location.path());
	    return active;
	  };

    $scope.changeLanguage = function (key) {
    	$translate.use(key);
  	};

    $scope.signOut = function() {

    }
  }
}

export default NavbarController;



