class TimetableController {
  	constructor($scope, $rootScope, myConfig, $http) {
	  	"ngInject";
	    this.name = 'timetable';
	    this.$rootScope = $rootScope;
	    this.$http = $http;
	    this.apiendpoint = myConfig.ApiEndpoint;

	  	let _this = this;
	  	this.getStudentTimetable(this.$rootScope.globals.currentUser.username).then(data => this.timetables = data);
  	}

  	getStudentTimetable(studentId) {
  		return this.$http.get(this.apiendpoint+"/timetable/student/"+studentId)
      	.then(response => response.data);
  	}


}

export default TimetableController;



