import angular from 'angular';

class AuthenticationService {
    constructor($http, $cookies, $rootScope, $timeout, myConfig) {
        "ngInject";
        this.$http = $http;
        this.$cookies = $cookies;
        this.$rootScope = $rootScope;
        this.$timeout = $timeout;
        
        this.apiendpoint = myConfig.ApiEndpoint;

    }

    Login(username, password, callback) {

        /* Use this for real authentication
         ----------------------------------------------*/
        this.$http({
            url: this.apiendpoint + '/get-token',
            method: "POST",
            headers: {
                //'Content-Type': 'application/x-www-form-urlencoded'
            },
            data: {
                'username': username,
                'password': password
            }
        }).then((response) => {
            if (response.status == 200) {
                callback(response);
            }
        });
    }

    SetCredentials(username,token) {

        this.$rootScope.globals = {
            currentUser: {
                username: username,
                credentials: token
            }
        };

        // set default auth header for http requests
        //this.$http.defaults.headers.common['Authorization'] = 'Bearer ' + token;
        this.$http.defaults.headers.common.Authorization = 'Bearer ' + token;

        // store user details in globals cookie that keeps user logged in for 1 week (or until they logout)
        var cookieExp = new Date();
        cookieExp.setMinutes(cookieExp.getMinutes() + 60);
        this.$cookies.putObject('globals', this.$rootScope.globals, { expires: cookieExp });
    }

    ClearCredentials() {
        this.$rootScope.globals = {};
        this.$cookies.remove('globals');
        //this.$http.defaults.headers.common.Authorization = 'Bearer';
    }
}

export default AuthenticationService;


