class LoginController {
  	constructor($scope, $location, AuthenticationService) {
  		"ngInject";
    	this.name = 'login';
    	this.$location = $location;
    	this.AuthenticationService = AuthenticationService;
	    // reset login status
	    this.AuthenticationService.ClearCredentials();
  	}

  	login() {
  		//this.user = {};
  		//console.log(this.user.username);
  		console.log("00000000");
	    this.AuthenticationService.Login(this.username, this.password, response => {
	    	if (response.data.length > 0) {
	    		console.log(response.data);
	    		this.AuthenticationService.SetCredentials(this.username, response.data);
	    		this.$location.path('/');
	    	} else {
	    		this.loginfail = true;
	    	}
	    });

	    // , (response) => {
	    	
	    //     if (response.success) {
	    //         AuthenticationService.SetCredentials(this.username, response.data);
	    //         $location.path('/');
	    //     } else {
	    //         //FlashService.Error(response.message);
	    //         //vm.dataLoading = false;
	    //     }
	    // });
	};
}

export default LoginController;



