class RegisterController {
  	constructor($scope, myConfig, $http) {
	  	"ngInject";
	  	this.$http = $http;
	    this.name = 'register';
	    this.apiendpoint = myConfig.ApiEndpoint;

	    /*$scope.changeLanguage = function (key) {
	    	$translate.use(key);
	  	};*/
	  	let _this = this;
	  	this.getTerms().then(data => this.terms = data);
	  	this.getCourses().then(data => this.courseList = data);
	  	//this.test = "ahihihi";
  	}

  	getTermNameFunc(termId) {
  		for (var i = 0; i < this.terms.length; i++) {
  			if (this.terms[i].id == termId) {
  				return this.terms[i].name;
  			}
  		}
  	}

  	getCourses() {
    	return this.$http.get(this.apiendpoint+"/course")
      	.then(response => response.data);
	}

	getTerms() {
		return this.$http.get(this.apiendpoint+"/term")
      	.then(response => response.data);
	}
}

export default RegisterController;



