class HomeController {
  	constructor($scope, $rootScope, myConfig, $http, $cookies) {
  		"ngInject";
    	this.name = 'home';
      this.$rootScope = $rootScope;
    	this.$http = $http;
      this.$cookies = $cookies;
	    this.apiendpoint = myConfig.ApiEndpoint;

	  	let _this = this;
	  	this.getStudentTimetable().then(data => this.timetables = data);
  	}

  	getStudentTimetable() {
  		// return this.$http.get(this.apiendpoint+"/timetable/student/"+this.$rootScope.globals.currentUser.username, 
    //   {
    //     //headers: {'Authorization': 'Bearer '+ this.$rootScope.globals.currentUser.credentials}
    //   }).then(response => response.data);

      return this.$http({
            url: this.apiendpoint + '/timetable/student/'+this.$rootScope.globals.currentUser.username,
            method: "GET",
            headers: {
            }
        }).then(response => response.data);
  	}


}

export default HomeController;



